<template>
  <div class="hot">
    <div class="hotop">
      <div class="hotopct">
        <div class="u-hmsprt hoticon"></div>
        <div class="hottime">更新日期：{{ timeTier() }}</div>
      </div>
    </div>
    <ul class="hot-songlist">
      <SongListItem
        v-for="(item, index) in hotsongs"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', $event);
          $emit('change-current-play-list', hotsongs);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt3: index < 3 }"
        >{{ (index + 1).toString().padStart(2, "0") }}</SongListItem
      >
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";

export default {
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  components: {
    SongListItem,
  },
  data() {
    return { hotsongs: null };
  },
  methods: {
    timeTier() {
      var da = new Date();
      var month = da.getMonth() + 1 + "月";
      var date = da.getDate() + "日";
      return [month, date].join("");
    },
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/artist/top/song?id=12279635").then((res) => {
      // console.log(res);
      this.hotsongs = res.data.songs;
    });
  },
};
</script>

